import React, { Component } from 'react'
import './App.css';
import UserInput from './Components/UserInput';
import UserOutput from './Components/UserOutput';

class App extends Component {

  state = {
    people: [
      {name: "Ada", age: 24},
      {name: "Chidi", age: 22},
      {name: "Arinze", age: 20}
    ]
  }

  changeName = () => {
      this.setState({
      people: [
        {name: "Adakaibeyamma", age: 24},
        {name: "Chidi", age: 22},
        {name: "Arinze", age: 20}
      ]
    } )
  }

  changedNameHandler = (event) => {
    this.setState({
      people: [
        {name: "Ada", age: 24},
        {name: event.target.value, age: 22},
        {name: "Arinze", age: 20}
      ]
    } )
  }

  render() {


    return (
      <div className="App">
         <h1>Hello from React</h1>
    
    <UserInput changed = {this.changedNameHandler} />
    <UserOutput name={this.state.people[0].name} age={this.state.people[0].age}/>
    <UserOutput name= {this.state.people[1].name} age= {this.state.people[1].age} changed = {this.changedNameHandler}/>
    <UserOutput name= {this.state.people[2].name} age= {this.state.people[2].age}/>
    <button className= "btn" onClick={this.changeName}>Change</button>
      </div>
    )
  }
}

export default App;

